"use client";
import { useState } from "react";

import {
  Home,
  Settings,
  HelpCircle,
  LogOut,
  ChevronRight,
  ChevronDown,
  ShoppingCart,
  Package,
  UserCheck,
  BarChart,
  Layers,
  Tag,
  Truck,
  CreditCard,
  Palette,
  User,
  Bell,
} from "lucide-react";
import BrandLogo from "./brandlogo";
import Link from "next/link";

export default function Sidebar() {
  const [isSidebarOpen, setSidebarOpen] = useState(false);
  const [isMobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [activeSubmenu, setActiveSubmenu] = useState<string | null>(null);
  const [activeMobileSubmenu, setActiveMobileSubmenu] = useState<string | null>(null);

  const toggleSubmenu = (menu: string) => {
    setActiveSubmenu(activeSubmenu === menu ? null : menu);
  };

  const toggleMobileSubmenu = (menu: string) => {
    setActiveMobileSubmenu(activeMobileSubmenu === menu ? null : menu);
  };

  const menus = [
    { name: "Dashboard", icon: Home, submenu: ["Overview", "Sales Stats", "Order Summary"], link: "/dashboard" },
    { name: "Orders", icon: ShoppingCart, submenu: ["All Orders", "Pending Orders", "Completed Orders", "Order History"], link: "/orders" },
    { name: "Products", icon: Package, submenu: ["All Products", "Add New Product", "Product Categories", "Inventory Management"], link: "/products" },
    { name: "Customers", icon: UserCheck, submenu: ["All Customers", "Add New Customer", "Customer Groups", "Customer Activity"], link: "/customers" },
    { name: "Analytics/Reports", icon: BarChart, submenu: ["Sales Reports", "Traffic Reports", "Product Performance", "Customer Reports"], link: "/analytics" },
    { name: "Categories", icon: Layers, submenu: ["All Categories", "Add New Category", "Category Analytics"], link: "/categories" },
    { name: "Coupons/Discounts", icon: Tag, submenu: ["Active Coupons", "Create New Coupon", "Expired Coupons", "Discount Campaigns"], link: "/coupons" },
    { name: "Shipping", icon: Truck, submenu: ["Shipping Methods", "Rates and Fees", "Shipping Labels", "Tracking"], link: "/shipping" },
    { name: "Payments", icon: CreditCard, submenu: ["Payment Gateways", "Transaction History", "Refunds", "Payment Settings"], link: "/payments" },
    { name: "Settings", icon: Settings, submenu: ["Store Settings", "Payment Settings", "Tax Settings", "Shipping Settings"], link: "/settings" },
    { name: "Theme Customization", icon: Palette, submenu: ["Change Theme", "Customize Colors", "Manage Fonts"], link: "/theme" },
    { name: "User Profile", icon: User, submenu: [], link: "/profile" },
    { name: "Notifications", icon: Bell, submenu: [], link: "/notifications" },
    { name: "Help & Support", icon: HelpCircle, submenu: ["Documentation", "Contact Support", "FAQ"], link: "/help" },
    { name: "Logout", icon: LogOut, submenu: [], link: "/logout" },
  ];

  return (
    <div className="relative flex">
      {/* Sidebar for Medium and Larger Screens */}
      <div
        className={`bg-white shadow-lg transform transition-all duration-300 ${isSidebarOpen ? "w-64" : "w-16"} hidden md:block`}
      >
        <div className="h-full flex flex-col items-center md:items-start">
          {isSidebarOpen && <div className="mb-4 mt-4"><BrandLogo /></div>}
          <ul className={`space-y-4 w-full ${isSidebarOpen ? "pl-2" : "mt-16"} flex flex-col`}>
            {menus.map((menu) => (
              <li key={menu.name} className="w-full">
                <Link href={menu.link}>
                  <div
                    className={`flex items-center ${isSidebarOpen ? "space-x-2" : "justify-center"} cursor-pointer`}
                    onClick={() => toggleSubmenu(menu.name)}
                  >
                    <menu.icon className="w-5 h-5 text-gray-600" />
                    {isSidebarOpen && (
                      <>
                        <span className="text-gray-700">{menu.name}</span>
                        {menu.submenu && (
                          <>
                            {activeSubmenu === menu.name ? (
                              <ChevronDown className="w-4 h-4 text-gray-600" />
                            ) : (
                              <ChevronRight className="w-4 h-4 text-gray-600" />
                            )}
                          </>
                        )}
                      </>
                    )}
                  </div>
                </Link>
                {menu.submenu && activeSubmenu === menu.name && isSidebarOpen && (
                  <ul className="pl-8 space-y-2 mt-2 text-sm text-gray-600">
                    {menu.submenu.map((subitem) => (
                      <li key={subitem}>
                        <Link href={`/${menu.name.toLowerCase().replace(" ", "-")}/${subitem.toLowerCase().replace(" ", "-")}`}>
                          {subitem}
                        </Link>
                      </li>
                    ))}
                  </ul>
                )}
              </li>
            ))}
          </ul>
        </div>
      </div>

      {/* Mobile Menu */}
      <div
        className={`fixed inset-0 bg-black bg-opacity-50 md:hidden ${isMobileMenuOpen ? "block" : "hidden"}`}
      >
        <div className="bg-white p-6 space-y-4">
          {/* Close Button */}
          <button
            onClick={() => setMobileMenuOpen(false)}
            className="text-gray-600 hover:text-gray-900 mb-4"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
          <div><BrandLogo /></div>
          {/* Menu List */}
          <ul className="space-y-4">
            {menus.map((menu) => (
              <li key={menu.name}>
                <Link href={menu.link}>
                  <div
                    className="flex justify-between items-center cursor-pointer"
                    onClick={() => toggleMobileSubmenu(menu.name)}
                  >
                    {/* Menu Name */}
                    <span className="text-gray-700 flex items-center">
                      <menu.icon className="w-5 h-5 mr-2 text-gray-600" /> {menu.name}
                    </span>

                    {/* Arrow Icon */}
                    {menu.submenu && (
                      <span>
                        {activeMobileSubmenu === menu.name ? (
                          <ChevronDown className="w-4 h-4 text-gray-600" />
                        ) : (
                          <ChevronRight className="w-4 h-4 text-gray-600" />
                        )}
                      </span>
                    )}
                  </div>
                </Link>

                {/* Submenu Items */}
                {menu.submenu && activeMobileSubmenu === menu.name && (
                  <ul className="pl-6 space-y-2 mt-2 text-sm text-gray-600">
                    {menu.submenu.map((subitem) => (
                      <li key={subitem} className="flex items-center">
                        <ChevronRight className="w-3 h-3 mr-2 text-gray-500" />
                        <Link href={`/${menu.name.toLowerCase().replace(" ", "-")}/${subitem.toLowerCase().replace(" ", "-")}`}>
                          {subitem}
                        </Link>
                      </li>
                    ))}
                  </ul>
                )}
              </li>
            ))}
          </ul>
        </div>
      </div>

      {/* Menu Icon for Small Screens */}
      <button
        onClick={() => setMobileMenuOpen(!isMobileMenuOpen)}
        className="absolute top-7 left-4 text-gray-600 hover:text-gray-900 md:hidden z-10"
      >
        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16m-7 6h7" />
        </svg>
      </button>

      {/* Large Screen Sidebar Toggle Button */}
      <button
        onClick={() => setSidebarOpen(!isSidebarOpen)}
        className="hidden md:block text-gray-600 hover:text-gray-900 absolute top-4 right-4 z-10"
      >
        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16m-7 6h7" />
        </svg>
      </button>
    </div>
  );
}
